      h2  Anlisis del entorno
    p En este primer momento del diagnstico, es importante delimitar la zona de estudio, la cual corresponde a una zona geogrfica en particular (un barrio, una vereda, un municipio, una regin, una ciudad). Al restringir el diagnstico a una zona en particular, se puede ser mucho ms eficiente en el proceso de captura y anlisis de la informacin, con lo cual se obtiene un diagnstico ms acertado. 
    p Analice, con atencin, los elementos principales que abarca el anlisis del entorno, los cuales dan cuenta de las realidades que configuran el territorio:
