      h2   Anlisis del sector productivo  
    .row
      .col-lg-7
        p Otro de los componentes importantes del diagnstico es el anlisis de sector productivo, el cual se enfoca en la descripcin de las caractersticas y componentes especficos de la actividad econmica en la cual tiene lugar la iniciativa de proyecto. 
        p Este anlisis se enfoca, sobre todo, en los comportamientos del mercado, la oferta y demanda de bienes y servicios que se da al interior del sector econmico, las relaciones comerciales y los encadenamientos productivos que se desarrollan alrededor de la iniciativa estudiada. 
        p Se presentan, en el siguiente recurso, los elementos clave que debe contener el anlisis del sector productivo para que aporte informacin relevante al proceso de formulacin del proyecto:
      .col-lg-5
        img(src='@/assets/curso/tema3/9.png' alt='AvatarTop')
    .row.justify-content-center.mt-5
      .col-lg-10.mt-5
        ImagenInfografica.color-acento-botones.mb-5
          template(v-slot:imagen)
            figure
              img(src='@/assets/curso/tema3/10.png', alt='Texto que describa la imagen')

          .tarjeta.color-acento-botones.p-3(x="0%" y="9%" numero="+")
            .h5.mb-2 Indicadores
            p En la formulacin de un proyecto, es indispensable analizar el sector productivo en el cual este tendr su aplicacin.
            P Para que el anlisis del sector productivo aporte informacin relevante para el proceso de formulacin del proyecto, debe contener elementos como:

          .tarjeta.color-acento-botones.p-3(x="0%" y="32%" numero="+")
            .h5.mb-2 1. Productividad del sector
            p Se deben revisar, de manera general, los volmenes de produccin en el contexto nacional, regional y/o municipal, para, de esta forma, establecer qu aspectos pueden favorecer o limitar su desarrollo. Esta informacion que puede proceder de fuentes primarias (encuestas, sondeos, entrevistas) o  de fuentes secundarias (estudios, tesis de grado, artculos, cofras oficiales), debe dar cuenta del estado actual del sector en trminos econmicos y monetarios.

          .tarjeta.color-acento-botones.p-3(x="0%" y="53%" numero="+")
            .h5.mb-2 2. reas destinadas a la actividad
            p Es importante establecer dnde se lleva a cabo la actividad productiva o en qu rea geogrfica este sector en particular tiene mayor participacin. Esto da una lectura general de las perspectivas y potencialidades de la actividad productiva.
          .tarjeta.color-acento-botones.p-3(x="0%" y="75%" numero="+")
            .h5.mb-2 3. Empleos generados
            p Un indicador de la importancia del sector productivo y su posicin dentro de la economa nacional es la identificacin de su aporte en la generacin de empleo, bien sea de forma directa o indirecta, para lo cual, es importante conocer los encadenamientos productivos. 
          .tarjeta.color-acento-botones.p-3(x="100%" y="75%" numero="+")
            .h5.mb-2 7. Relacin con objetivos de desarrollo y planes de gobierno
            p De esta forma, ser posible identificar la oferta institucional aplicable al sector y conseguir el apoyo necesario para adelantar las ideas de negocio relacionadas con el entorno productivo.  
          .tarjeta.color-acento-botones.p-3(x="100%" y="53%" numero="+")
            .h5.mb-2 6. Estudio de necesidades
            p Es importante conocer cules son las limitantes del sector productivo, para, de esta forma, disear estrategias que permitan superarlas y atenderlas de manera oportuna. 
          .tarjeta.color-acento-botones.p-3(x="100%" y="32%" numero="+")
            .h5.mb-2 5. Aporte al producto interno bruto
            p Con la identificacin de los volmenes de produccin y de comercializacin, es posible establecer los aportes de ese sector de la economa al producto interno bruto del pas, lo cual es fundamental para la gestin de recursos y el reconocimiento del sector en el mbito de la economa nacional. 
          .tarjeta.color-acento-botones.p-3(x="100%" y="9%" numero="+")
            .h5.mb-2 4. Volmenes de comercializacin
            p Este dato entrega una medida de los recursos anuales que se transan alrededor del sector productivo analizado, lo cual se puede emplear para determinar su importancia dentro de la economa nacional, regional o municipal. 
    #t_9_9.titulo-segundo.color-acento-contenido.mt-5
      h2  Recoleccin de informacin
    p La recoleccin de informacin es una de las etapas ms importantes del proceso de diagnstico, debido a que la calidad del producto final, el planteamiento de los problemas y las alternativas de solucin dependen del tipo de informacin que se recoja, de sus caractersticas y de la forma como se organizan los datos para su posterior anlisis. 
    p Para la recoleccin de informacin diagnstica, en la formulacin de proyectos, es necesario tener en cuenta los elementos que se describen a continuacin:
    SlyderB.mb-5.mt-5.b.p-4(:datos="datosSlyder")
