          p Las herramientas para el procesamiento de datos son muy diversas y sus aplicaciones son casi que ilimitadas. Dependiendo de los objetivos del estudio o del anlisis, existen herramientas casi que a la medida de cada necesidad, por medio de las cuales es posible procesar los datos y obtener como resultado tendencias, reportes, comparaciones y salidas, que facilitan la comprensin de la realidad y la interpretacin de las dinmicas que se dan en el territorio.
          p Le presentamos las principales utilidades del procesamiento de datos; explore el recurso que se muestra a continuacin y procure llevar registro de lo ms destacado en su libreta personal de apuntes:
