            .col-lg-12.texto1.p-4
              p La matriz de marco lgico est diseada para resolver problemas, por lo que su planteamiento es la base para el proceso de formulacin del proyecto o la satisfaccin de una necesidad manifiesta. Los proyectos tienen la finalidad de sobrepasar esos obstculos o barreras para llegar a una situacin potencial o deseada.
          p.mt-4 Para este paso inicial, del planteamiento del problema o necesidad, es importante tener en cuenta tres elementos esenciales:
          ul.lista-ul.mt-4
            li.mb-0 
              i.fas.fa-angle-right.color-acento-contenido
