            li.mb-0 
              i.fas.fa-angle-right.color-acento-contenido
              p.mb-0 <strong>Situacin potencial:</strong>  cmo se esperara que est la situacin en un tiempo determinado. 
            li.mb-0 
              i.fas.fa-angle-right.color-acento-contenido
              p.mb-0 <strong>Barreras: </strong>  obstculos que existen para que la situacin actual no est como la situacin potencial.  

        
        .col-lg-5.tt
