      #t_9_9.titulo-segundo.color-acento-contenido.mt-5
        h2  Identificar el problema
      p Aparentemente, identificar un problema es una tarea sencilla, pues en el da a da cualquier persona tiene situaciones difciles a las cuales enfrentarse, lo cual, necesariamente, requiere identificarlas y actuar de acuerdo con lo registrado en sus observaciones. Sin embargo, delimitar un problema es un ejercicio mucho ms complejo de lo que comnmente se cree, debido a que a menudo se confunden sus causas con sus consecuencias.
      p Por fortuna, en el proceso de formulacin de proyectos, existen herramientas que dirigen el trabajo y facilitan la identificacin y delimitacin de los problemas, con lo cual es posible, a travs del proyecto, atender las situaciones (obstculos) y facilitar el trnsito hacia una situacin potencial o deseada. 
      p Independientemente del tipo de diagnstico que se realice, es necesario que, en la identificacin del problema, se respondan las siguientes preguntas:
      .row.mt-5
        .col-lg-6
          .row.mb-5
            .col-md-12.mb-5.mb-md-0
              LineaTiempoD.color-primario
                p(numero="1" titulo="En qu consiste el problema?") Se debe describir qu obstculos se interponen para llegar a la situacin deseada.
                
                p(numero="2" titulo="Cmo se evidencia el problema?") Establecer cmo se hace visible el problema dentro de la comunidad o zona afectada.
                
                p(numero="3" titulo="Qu causa el problema?") Establecer las causas inmediatas, aquello que lo produce.
                
                p(numero="4" titulo="Qu factores agravan la situacin?") Qu elementos, de alguna u otra forma, hacen que la situacin empeore.
        .col-lg-1
        .col-lg-4
          img(src='@/assets/curso/tema3/19.svg' alt='AvatarTop')
        .col-lg-1
      p.mt-4 A continuacin, se consideran algunos problemas identificados de manera correcta e incorrecta:
      .titulo-sexto.color-acento-botones.mt-5
        h5 Tabla 2
        span   Manera correcta e incorrecta de identificar un problema central
