          p.mt-2 Una vez se ha identificado el problema central, tenga en cuenta que:
          ul.lista-ul.mt-4
            li.mb-0 
              i.fas.fa-angle-right.color-acento-contenido
              p Es necesario empezar a establecer sus causas y sus consecuencias.
            li.mb-0 
              i.fas.fa-angle-right.color-acento-contenido
              p Entre ms informacin se tenga sobre el problema y sus orgenes, es posible desarrollar alternativas de solucin.
            li.mb-0 
              i.fas.fa-angle-right.color-acento-contenido
              p Las alternativas de solucin deben permitir actuar sobre los elementos que lo originan y no sobre las situaciones que de all se derivan.
            li.mb-0 
              i.fas.fa-angle-right.color-acento-contenido
              p Cuando un problema central no se identifica de manera correcta, la formulacin de los indicadores igualmente ser incorrecta.
            li.mb-0 
              i.fas.fa-angle-right.color-acento-contenido
              p Si los indicadores estn incorrectamente formulados, se carecer de efectividad en la solucin de las problemticas.
            li.mb-0 
              i.fas.fa-angle-right.color-acento-contenido
              p Si no hay efectividad en la solucin de problemticas, no se lograrn los objetivos del proyecto, entre otras dificultades.
            li.mb-0 
              i.fas.fa-angle-right.color-acento-contenido
              p No lograr los objetivos del proyecto es muy grave, ya que implica una mala utilizacin de los recursos y el incumplimiento de los compromisos adquiridos con las comunidades. 
    #t_9_9.titulo-segundo.color-acento-contenido.mt-5
      h2  rbol de problemas
    p El rbol de problemas trata de <strong>la identificacin del problema central</strong> y de todos los elementos que, de una u otra forma, se relacionan con este (causas y efectos). Se usa para establecer qu est originando la situacin (causas directas e indirectas) y la manera como dicha situacin repercute en otros niveles de la comunidad o el rea geogrfica estudiada (efectos directos e indirectos).
    .titulo-sexto.color-acento-botones.mt-5
      h5 Figura 2
      span  Estructura del rbol de problemas para elaboracin de proyectos
    img(src='@/assets/curso/tema3/ss.svg' alt='AvatarTop')
    p.mt-5 Con la elaboracin del rbol de problemas, se allana el camino para la identificacin de alternativas de solucin a la problemtica planteada. 
    .row.mt-5
      .col-lg-7
        ol.lista-ol--cuadro
          li 
            .lista-ol--cuadro__vineta
            h4 Por qu rbol de problemas?
          p El rbol de problemas se denomina de esta manera debido a que existen mltiples ramificaciones que representan las relaciones entre las causas (pensadas como races del problema) y sus efectos (entendidas como ramas superiores). 
        ol.lista-ol--cuadro.mt-5
          li 
            .lista-ol--cuadro__vineta
            h4 Cmo se disea este rbol?
          p En el anlisis de situaciones reales, es posible que un problema central tenga varias causas directas y que estas, a su vez, tengan una misma causa indirecta, por lo que, en cuanto a la forma como se construye el rbol, no hay un diseo predeterminado, sino que, por el contrario, este es un reflejo de las condiciones particulares de cada entorno analizado.

      .col-lg-5
        .row.justify-content-center
          .col-lg-10
            img(src='@/assets/curso/tema3/21.svg' alt='AvatarTop')
