            <td colspan="2"> Falta de recursos econmicos y de asistencia tcnica predial para el mejoramiento de los cultivos de caf.</td>
            <td colspan="3"> Falta de recursos para desarrollar procesos de renovacin de cafetales.</td>


    .row.justify-content-center.mt-5
      .col-lg-7.mt-5
        p En la construccin del rbol de problemas, es fundamental reflexionar sobre:
        ul.lista-ul.mt-4
          li.mb-0 
            i.fas.fa-check.color-acento-contenido
            p <Strong>El problema est bien expresado?</strong> Para que el problema este bien expresado, debe presentarse como una situacin negativa.
          li.mb-0 
            i.fas.fa-check.color-acento-contenido
            p <Strong>A quines afecta?</Strong> Analizar si afecta solo a un sector de la poblacin, a un nmero determinado de personas o a una comunidad. 
          li.mb-0 
            i.fas.fa-check.color-acento-contenido
            p <Strong>Cmo afecta?</Strong> Definir cmo el problema genera los impactos mencionados. 
          li.mb-0 
            i.fas.fa-check.color-acento-contenido
            p <Strong>Desde cundo afecta?</Strong> Establecer el tiempo que esa situacin en particular lleva en el territorio. 
          li.mb-0 
            i.fas.fa-check.color-acento-contenido
            p <Strong>Es una situacin modificable?</Strong> Revisar si la problemtica se puede revertir o si se pueden reducir sus impactos. 
          li.mb-0 
            i.fas.fa-check.color-acento-contenido
            p <Strong>Qu interrelacin tiene con otros problemas?</Strong> Identificar relaciones de causalidad.  
