        img(src='@/assets/curso/tema3/22.svg' alt='AvatarTop')
    .row.justify-content-center.mt-5
      .col-lg-8
        .cajon.color-acento-contenido.p-4.mb-4
          .h4 Atencin!
          p La construccin del rbol de problemas se realiza con el fin de identificar las causas del problema central, para, de esta forma, acceder a ubicar las mejores alternativas de solucin.
