                  p.mt-4 En la justificacin, se debe mencionar qu aspectos mejorarn con la implementacin del proyecto, no solo en lo relacionado con el logro de los objetivos, sino con los impactos adicionales que su implementacin tendr sobre una comunidad, un territorio o un segmento de la poblacin.

