        .row.texto11
          .col-lg-12.texto1.p-3
            p En la formulacin de los proyectos, es necesario identificar las alternativas de solucin por medio de las cuales se superarn las barreras y se llegar, finalmente, a la situacin esperada o potencial. Para esto, se debe tomar como base el rbol de problemas y, a partir de este, elaborar un rbol de objetivos, el cual se constituye en un esquema donde se visualizan las condiciones que se deben cumplir para lograr ese estado deseado.
        p.mt-4 Con base en la Metodologa de Marco Lgico (MML), el objetivo general del proyecto tiene su origen al convertir en positivo el problema central del rbol de problemas. Igualmente, los objetivos especficos, tambin denominados medios, se dan al pasar a positivo las causas del problema, mientras que los efectos directos e indirectos se transforman en fines directos e indirectos del proyecto, respectivamente. 
        p De ah la importancia de construir un rbol de problemas consistente y basado en las situaciones identificadas en el proceso de diagnstico, tal como se presenta en el siguiente esquema de problemas y objetivos:
