      h2  3.5   Anlisis de alternativas
    .row
      .col-lg-8.mt-5
        p Las alternativas son las diferentes opciones que existen para atender un problema o satisfacer una necesidad. Incluso, se considera que son las distintas formas que se tienen para alcanzar un objetivo determinado. Por lo tanto, el anlisis de alternativas consiste en la comparacin de las diferentes opciones que se tienen para solucionar el problema, utilizando criterios tcnicos, ambientales, financieros o tecnolgicos, entre otros.
        p A modo de ejercicio, y para afianzar la comprensin del anlisis de alternativas, considere, en el siguiente cuadro, las alternativas propuestas al problema planteado y determine, para usted, cul sera la ms apropiada.
