                .h4 Por qu?
                p Al momento de seleccionar la alternativa, es importante sealar por qu se escoge esa y no otra de las que se tenan perfiladas en el anlisis inicial, teniendo en cuenta razones tcnicas, operativas, financieras o los riesgos asociados con su implementacin.
              
          .row.justify-content-center.mb-3
            .col-lg-12
              .cajon.color-acento-contenido.p-4.mb-4
                .h4 Explicacin
                p Elegida una de las alternativas, se procede con su explicacin, que es el espacio donde se exponen los detalles relacionados con su implementacin, los alcances, las razones que se tuvieron en cuenta para su eleccin y qu elementos presentes en el diagnstico respaldan esta decisin.
              
          .row.justify-content-center.mb-3
            .col-lg-12
              .cajon.color-acento-contenido.p-4.mb-4
                .h4 Indicar estrategias
                p Posteriormente, se expone un resumen de la alternativa, indicando las estrategias que se tendrn en cuenta para la solucin del problema o la satisfaccin de las necesidades de las comunidades con la ejecucin del proyecto. 
              
