                p(numero="1" titulo="Viabilidad tcnica") Se elige la alternativa que desde el punto de vista tcnico sea la ms indicada para implementar. 
                
                p(numero="2" titulo="Aceptabilidad por parte de las comunidades") Debe ser una alternativa que vaya en sintona con los usos y costumbres de una comunidad en particular. 
                
                p(numero="3" titulo="Financiamiento requerido frente al disponible") Es importante verificar si la disponibilidad de recursos es suficiente para cubrir la alternativa seleccionada. 
                
                p(numero="4" titulo="Capacidad institucional") Establecer si la entidad operadora est en capacidad de desarrollar la alternativa seleccionada. 
                p(numero="5" titulo="Impacto ambiental") Se recomienda implementar las actividades que generen el menor impacto ambiental posible. 
    
