      h2  3.6   Definicin de actividades
    p Una vez se han identificado los objetivos especficos del proyecto y se ha priorizado la alternativa de solucin, es necesario definir las acciones que permitirn, finalmente, materializar las estrategias propuestas. Para determinar las acciones que tendrn lugar dentro del proyecto, se tomarn los objetivos especficos y se describirn las acciones necesarias para lograrlos.
    p Los responsables de la ejecucin del proyecto se deben encargar de que todas las actividades propuestas en la planeacin se lleven a cabo en el plano real, pues es la nica forma de que se produzcan los resultados esperados, se logren los objetivos especficos y se ataquen las causas del problema central.
