      h2  3.8   Metas, indicadores y medios de verificacin
    p Las metas constituyen propsitos medibles, por medio de los cuales se alcanzan los objetivos del proyecto. Estas se pueden establecer, en cuanto a productos puntuales esperados, con la ejecucin del proyecto o, igualmente, respecto a resultados con relacin a su impacto en el logro de los objetivos.
