      p.mt-5 Por su parte, un indicador es una representacin establecida mediante la relacin entre dos o ms variables, a partir del cual se registra, procesa y presenta informacin relevante, con el fin de medir el avance o retroceso en el logro de un determinado objetivo, en un periodo de tiempo determinado (APC Colombia 2020). 
